@mixin font($size, $weight, $line-height) {
  font-size: $size;
  font-weight: $weight;
  line-height: $line-height;
}

@mixin container($max-width, $padding) {
  margin: 0 auto;
  max-width: $max-width;
  padding-left: $padding;
  padding-right: $padding;
}

@mixin pm-zero($margin: 0, $padding: 0) {
  margin: $margin;
  padding: $padding;
}

@mixin button($width: 234px) {
  box-sizing: border-box;
  width: $width;
  padding-top: 20px;
  padding-right: 52px;
  padding-bottom: 19px;
  padding-left: 53px;

  border: 4px solid $border-main;
  border-radius: 0;

  @include font(12px, 700, 14px);
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  color: $text-main;

  background-color: transparent;
}

@mixin bg-img($url) {
  background-image: url($url);
  background-repeat: no-repeat;
  background-position: 0 0;
}
